@import url(https://fonts.googleapis.com/css2?family=Bad+Script&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
}
.App{
  background-image: url("https://github.com/parth-kabra/My-Profile/blob/main/assests/tumblr_nymyb4SwV21rldv4go1_1280.gif?raw=true");
  width: 100%;
  height: 100vh;
  background-repeat:repeat-x;
}
.app > a > img{
  position:fixed;
  right:10px;
  bottom: 10px;
  width:13vh;
  cursor:pointer;
  transition: width 1s;
}
.app > a > img:hover{
  width:15vh
}
html {
  scroll-behavior: smooth;
}

.page{
  width:100%;
  height: 40vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  color: white;
  margin-left: -1vw !important;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
}
.options > h3{
  margin-left: 4vw;
  cursor: pointer;
  font-size:3vh;
}
.header > h3{
  cursor: pointer;
  margin-right: 16vw;
  font-size:3vh;
}
.title{
  margin-left:3vh !important
}
.pp{
  margin-top:1pc;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
  margin-left: -3vw !important;
  display:-webkit-flex;
  display:flex;
  -webkit-justify-content:flex-start !important;
          justify-content:flex-start !important;
}
.page > h1{
  font-size: 12vh
}
.page > p{
  font-size: 3vh;
  font-weight: 400
}
.page > p > a{
  color:aqua;
  font-weight:bold;
  text-decoration:none;
}
.page > p > a:hover{
  text-decoration:underline;
}
.title {
  border-right: solid 3px white;
  white-space: nowrap;
  overflow: hidden;
  font-size: 28px;
}

.title {
  -webkit-animation: animated-text 4s steps(29,end) 1s 1 normal both,
  animated-cursor 600ms steps(29,end) infinite;
          animation: animated-text 4s steps(29,end) 1s 1 normal both,
  animated-cursor 600ms steps(29,end) infinite;
}
@-webkit-keyframes animated-text{
  from{width: 0;}
  to{width: 433px;}
}
@keyframes animated-text{
  from{width: 0;}
  to{width: 433px;}
}
@-webkit-keyframes animated-cursor{
  from{border-right-color: white;}
  to{border-right-color: transparent;}
}
@keyframes animated-cursor{
  from{border-right-color: white;}
  to{border-right-color: transparent;}
}
.header,.options{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}
.options,.header{
  color: white;
  font-family: "Arial";
}
.options > p{
  margin-left: 4vw;
  cursor: pointer;
  font-size:3vh;
}
.header > p{
  cursor: pointer;
  margin-right: 16vw;
  font-size:3vh;
}
.header{
  height:5pc;
  -webkit-align-items: center;
          align-items: center
}
.options{
  margin-left: 4vw
}

.contact > h1{
    font-family: "Bad Script";
    margin:2vh
}
.skills > a > img{
    width:15vh;
    margin:6vh;
    transition: width 1s;
}
.skills > a > img:hover{
    width:17vh
}
.ss > a > img{
    width:16vh;
    margin:6vh;
}
.ss > a > img:hover{
    width:18vh
}
.skills{
    transition: all 1s;
}

.contact{
    background-color:#e4e6dd;
    width: 100%;
    -webkit-align-items:center;
            align-items:center;
    -webkit-justify-content:center;
            justify-content:center;
    display:-webkit-flex;
    display:flex;
    -webkit-flex-direction:column;
            flex-direction:column;
}

.about >  p{
    font-family: "Bad Script";
    font-size:10rem;
    padding: 5pc;
}
.buc > a{
    margin-top: -1pc;
    font-size:1.3em;
    -webkit-animation: 
    typing 3.5s steps(30, end),
    blink-caret .5s step-end infinite;
            animation: 
    typing 3.5s steps(30, end),
    blink-caret .5s step-end infinite;
}

.icons{
    color:rgb(29, 29, 29) !important;
    text-decoration:none;
    margin-top: 1pc;
}
.icons > a{
    margin-left: 1pc;
    cursor: pointer;
    transition: width 1s;
}
.about {
    width:100%;
    height:83vh;
    display:-webkit-flex;
    display:flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content:center;
            justify-content:center;
    text-align: center;
    -webkit-align-items: center;
            align-items: center;
    background-color: #ededed;
}
.buc{
    display:-webkit-flex;
    display:flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

